.app__about{
    flex: 1;
    width: 100%;
    flex-direction: column;
}

.app__profiles{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-top: 2rem;
}

.app__profile-item{
    width: 130px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    margin: 3rem;

    img{
        width: 100%;
        min-height: 110px;
        border-radius: 15px;
        object-fit: cover;
    }

    @media screen and (min-width: 2000px){
        width: 370px;
        margin: 2rem 4rem;

        img{
            width: 100%;
            height: 320px;
            border-radius: 15px;
            object-fit: cover;
        }
    }
}